.tutorslist--wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  margin-top: 10vh;
}

.title-section {
  display: flex;
  justify-content: center;
  align-items: center;

  color: white;
}

.title {
  font-size: 1.8rem;
}

.title .bigger {
  font-size: 2.4rem;
}

.filter-teacher {
  margin-left: 2rem;
}

.tutors--list {
  margin: 3rem;

  display: grid;
  grid-template-columns: repeat(auto-fill, 20rem);
  grid-column-gap: 2rem;
  grid-row-gap: 2rem;
  align-content: center;
  justify-content: center;
}

.tutor-tile-container {
  /* border: 2px black solid; */
  height: 20rem;
  /* width: 20rem; */
  display: flex;
  flex-direction: column;
}

.tutor-tile-container p {
  background-color: white;
}

.tutor-tile-container img {
  flex: 1;
  width: 100%;
}
