.nav-bar {
  display: flex;
  min-height: 10vh;
  position: fixed;
  top: 0;
  width: 100vw;
  align-items: center;
  justify-content: space-around;
  background-color: transparent;
  transition: all 0.3s ease-in;
  z-index: 99;
}

.nav-bar--bg {
  background-color: black;
  opacity: 0.9;
  position: absolute;
  top: 0;
  height: 10vh;
  width: 100vw;
  z-index: -1;
}

.nav-links {
  width: 100%;
  display: flex;
  justify-content: space-around;
  color: white;
  list-style: none;
}

.nav-bar .nav-links .home {
  width: 80vw;
  background-color: teal;
}

.nav-link {
  list-style: none;
  text-decoration: none;
  color: white;
  transition: all 0.3s ease-in;
  cursor: pointer;
  font-size: 1.2rem;
}

.nav-link:hover {
  /* font-size: 1.4rem; */
  text-decoration: none;
}

.link-active {
  /* color: var(--primary-color); */
}

.bottom_bar {
  background: white;
  width: 100%;
  height: 0.03rem;
  transition: transform 0.3s ease-in;
  transform: scaleX(0);
}

.bottom_bar.link-active {
  transform: scaleX(0.8);
}
