.tutors--wrapper {
  display: flex;
  height: 100vh;
  padding-top: 10vh;
}
.hexagon-container {
  flex: 1.5;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.hex0 {
  z-index: 1;
}

.hex1 {
  transform: translateY(-102%);
}

.hex2 {
  transform: translateX(-78%) translateY(-51%);
}

.hex3 {
  transform: translateX(-78%) translateY(51%);
}
.hex4 {
  transform: translateY(102%);
}
.hex5 {
  transform: translateX(78%) translateY(51%);
}

.hex6 {
  transform: translateX(78%) translateY(-51%);
}

.hexagon {
  height: 10rem;
  width: 10rem;
  position: absolute;

  clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
  /* background-color: red; */
  overflow: hidden;
}

.hexagon img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: 0.3s ease-in;
  position: absolute;
  top: 0;
  left: 0;
}

.hexagon img:hover {
  transform: scale(1.2);
  border: 0px;
}

.hexagon .wrapper {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hexagon p {
  z-index: 1;
  color: white;
  font-size: 1rem;
  padding: 0 2rem;
}

.search--container {
  flex: 1;
  background: teal;
}
