.tutor-profile--main {
  height: 100vh;
  padding-top: 10vh;
  display: flex;
}

.tutor-profile--main .slider {
  height: 90vh;
  width: 100%;
  position: absolute;
}

.profile--image-container {
  height: 100%;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 768px) {
  .profile--image-container {
    flex-direction: column;
  }
}

.profile--description {
  height: 100%;
  flex: 1;
  background-color: teal;
}

.profile--image-container .hex0 {
  z-index: 1;
}

.profile--image-container .hex1 {
  transform: translateY(-102%);
}

.profile--image-container .hex2 {
  transform: translateX(-78%) translateY(-51%);
}

.profile--image-container .hex3 {
  transform: translateX(-78%) translateY(51%);
}
.profile--image-container.hex4 {
  transform: translateY(102%);
}
.profile--image-container .hex5 {
  transform: translateX(78%) translateY(51%);
}

.profile--image-container .hex6 {
  transform: translateX(78%) translateY(-51%);
}

.profile--image-container .hexagon {
  height: 10rem;
  width: 10rem;
  position: absolute;

  clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
  background-color: black;
  overflow: hidden;
}

.profile--image-container .hexagon img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: 0.3s ease-in;
  position: absolute;
  top: 0;
  left: 0;
}

.profile--image-container .hexagon img:hover {
  transform: scale(1.2);
  border: 0px;
}

.profile--image-container .hexagon .wrapper {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile--image-container .hexagon p {
  z-index: 1;
  color: white;
  font-size: 1rem;
  padding: 0 2rem;
}

.profile--image-container .hexagon .darken {
  opacity: 0.5;
}

.profile--description--wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.profile--description--wrapper .card {
  font-size: 1.4rem;
  background-color: white;
  padding: 0.3rem 2rem;
  margin: 0.5rem;
}

.profile--description--wrapper .bigger {
  font-size: 2rem;
}

.profile--description--wrapper .fill-gap {
  flex: 1;
}

.tutor-profile--secondary {
  height: 100vh;
  width: 100%;
  padding-top: 10vh;

  display: flex;
}

.about-tutor--video {
  flex: 1;
  height: 100%;
}

.about-tutor--video iframe {
  height: 66.5%;
  width: 100%;
  padding: 2rem;
}

.tutor-profile--secondary .timetable--container {
  flex: 1;
  height: 100%;
}
