.home-wrapper {
  height: 100vh;
  padding-top: 10vh;
  padding-bottom: 2rem;
  display: flex;
}

.left-home {
  width: 50vw;
  height: 100%;
  align-items: center;

  display: flex;
  flex-flow: column;
}

.redirect-img-container {
  height: 20%;
  margin: auto auto;

  cursor: pointer;
}

.redirect-img-container img {
  height: 80%;
  transition: transform 0.3s ease-in;
}

.redirect-img-container p {
  color: white;

  transition: transform 0.3s ease-in;
  font-size: 1.6rem;
}

.redirect-img-container:hover p {
  color: var(--primary-color);
}

.redirect-img-container:hover img {
  transform: scale(0.9);
}

.horizontal-break {
  height: 0.15rem;
  background: #9b9b9b;
  width: 70%;
}

.right-home {
  width: 100%;
  height: 100%;
  display: flex;
}

.home-main-container {
  width: 100%;
  display: flex;
  flex-flow: column;
}

.home-main-container p {
  color: white;
  margin-top: 16px;
  font-size: 1.3rem;
}

.home-video-container {
  margin: 16px auto;
  height: 40vh;
  width: 88.88vh;
}

.gallery-container {
  height: 220px;
  margin-bottom: 2rem;
  margin: 0px 3rem;
}
.fill-gap {
  flex: 1;
}
.carousel {
  height: 220px;
}

.gallery--image {
  height: 100%;
  width: 100%;
}

.social-link-container {
  width: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 20rem;
}

a.social {
  margin: 1rem 1rem;
  transition: transform 250ms;
  display: inline-block;
}
a.social:hover {
  transform: translateY(-2px);
}

a.youtube {
  color: #eb3223;
}

a.facebook {
  color: #4968ad;
}

a.twitter {
  color: #49a1eb;
}

a.instagram {
  color: white;
}
