.about-container section {
  height: 100vh;
  width: 100%;
  /* border: 10px black solid; */
  display: flex;
  flex-direction: column;
  /* background-color: teal; */
}

#services {
  height: 90vh;
}

.about--title {
  color: white;
  font-size: 1.3rem;
  margin-top: 0.8rem;
}

.about--wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.about--card--container {
  height: 100%;
  width: 100%;
  flex: 1;
  display: flex;

  padding: 2rem;
}

.about--card--gallery {
  height: 100%;
  width: 100%;
  background: red;
}

.space-for-two-container {
  flex: 1;
  display: flex;
  padding: 2rem 0;
}

.space-for-container {
  margin: 0 1rem;
  background: blue;
  height: 100%;
  width: 100%;
}

.services--title {
  color: white;
  font-size: 2.7rem;
  text-align: left;
  margin-left: 1rem;
}
.services--wrapper {
  margin-top: 2rem;
  display: flex;
  width: 100%;
  flex: 1;
}
.services--img {
  flex: 0.6;
}
.services--img img {
  height: 70%;
  width: 100%;
  object-fit: cover;

  padding: 0 1rem;
}
.services-list--wrapper {
  flex: 1;
  width: 100%;
  justify-content: flex-end;
  padding-right: 1rem;
}

.services--list-container {
  display: grid;
  background: rgba(0, 0, 0, 0.75);
  grid-template-columns: repeat(2, 1fr);
}

@media (max-width: 768px) {
  .services--list-container {
    grid-template-columns: repeat(1, 1fr);
  }
}

.service-item {
  color: white;
  display: flex;
  padding: 0 1rem;
  border-top: 2px #9b9b9b solid;
}
.service-item-number {
  font-size: 3rem;
}
.service-item-title {
  margin-top: 0.5rem;
  margin-left: 1rem;
  color: white;
}

.about--card--container p {
  color: white;
  font-size: 1.3rem;
}
