.welcome--slider {
  height: 100vh;
  width: 100vw;
  color: white;
  display: flex;
  flex-direction: column;
  background-color: black;
  background-image: url("../../static/images/background-1.png");
  background-size: cover;
}
.welcome--slider img {
  height: 50vh;
  width: 50vh;
}
.welcome--slider .bottom {
  bottom: 2rem;
  margin-bottom: 2rem;
}
.welcome--slider .center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
}
.welcome--slider .bottom p {
  font-size: 1.6rem;
  color: rgba(255, 255, 255, 0.7);
}
.welcome--slider .bottom .circles {
  display: flex;
  justify-content: center;
  margin: 0.5rem 0;
}
.welcome--slider .bottom .circle {
  height: 1.5rem;
  width: 1.5rem;
  margin: 0 1rem;
  background: rgba(255, 255, 255, 0.5);
  clip-path: circle();
}

.welcome--slider .bottom .circles .highlight {
  background: white;
}
