.carousel,
.carousel--container {
  width: 100%;
  height: 220px;
}

.carousel--container {
  display: flex;
}

.carousel {
  overflow: hidden;
}
.carousel-card {
  display: flex;

  height: 220px;
  justify-content: center;
  align-items: center;

  flex-basis: 100%;
  flex-grow: 0;
  flex-shrink: 0;
  background-color: black;
}
