.contact--wrapper {
  height: 100vh;
  padding-top: 10vh;
  display: flex;
}
.contact--wrapper .image-section--wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contact--wrapper .half-page {
  width: 50vw;
  height: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.contact--wrapper .image-section {
  font-size: 3vh;
  width: 100%;
  margin: 1rem;
  padding: 1rem;
  color: white;
  position: relative;
}

.contact--wrapper .image-section p {
  height: 100%;
  width: 100%;
}

.contact--wrapper .image-section .image-section--background,
.contact--wrapper .image-section img {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: -2;
  position: absolute;
  /* background-image: url("https://www.northeastern.edu/graduate/blog/wp-content/uploads/2019/06/Equity-in-the-Classroom-FACEBOOK.jpeg"); */
}

.contact--wrapper .image-section img {
  object-fit: cover;
}

.contact--wrapper .image-section .image-section--background {
  background-color: black;
  z-index: -1;
  opacity: 0.7;
}

.contact-number--wrapper {
  margin-bottom: 3rem;
  font-size: 1rem;
  color: white;
}
